* {
  padding: 0;
  margin: 0;
}

html {
  font-size: 18px;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

.hidden {
  display: none;
}

.content {
  margin: auto auto auto auto;
  text-align: center;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.fixed {
  position: fixed;
}

.flex {
  display: flex;
}
.flex__center {
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.flex__between {
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: baseline;
}
.flex__column {
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
}
.flex__wrap {
  flex-flow: row wrap;
  align-items: flex-start;
}
.flex--cl {
  flex-flow: column nowrap;
  justify-content: space-evenly;
}
.flex--rw {
  flex-flow: row nowrap;
}

.ground {
  background-color: green;
}

.border {
  border: solid 2px black;
}

.regular {
  font-family: "Barlow", sans-serif;
  font-weight: 600;
}
.regular--logo {
  font-weight: 800;
}
.regular--heavy {
  font-weight: 700;
}

.bold {
  font-family: "Fraunces", serif;
  font-weight: 700;
}

.black {
  font-family: "Fraunces", serif;
  font-weight: 900;
}

.toggle > span {
  display: block;
  width: 25px;
  height: 2px;
  background-color: white;
  transition: all 300ms ease-in-out;
  transform-origin: 3px 1px;
}

.toggle--closed > span:not(:last-child) {
  margin-bottom: 5px;
}

.toggle--open {
  transform-origin: 3px 1px;
}
.toggle--open > span:first-child {
  transform: rotate(45deg);
}
.toggle--open > span:nth-child(2) {
  opacity: 0;
}
.toggle--open > span:last-child {
  transform: rotate(-45deg);
}

.clients {
  text-align: center;
}
.clients__title {
  text-align: center;
  font-size: 0.8888888889rem;
  color: #a7abae;
  letter-spacing: 6px;
}
.clients__image {
  width: 50px;
  border-radius: 100%;
  align-self: center;
}
.clients__paragraph {
  font-size: 1rem;
  color: #5a636c;
}
.clients__name {
  font-size: 1.3333333333rem;
  color: black;
}
.clients__occ {
  font-size: 0.8888888889rem;
  color: #a7abae;
}

.container__clients {
  width: 100%;
  height: auto;
  margin-bottom: 5.3333333333rem;
}
.container__clients__cards {
  background-color: inherit;
  gap: 3.7777777778rem;
}

.content__clients {
  width: 17.6666666667rem;
  height: auto;
  gap: 40px;
}
.content__clients__info {
  width: inherit;
  height: auto;
  gap: 0.8888888889rem;
}
.content__clients__title {
  width: 100%;
  height: 8.2222222222rem;
}

.inner {
  overflow-y: hidden;
  height: 100vh;
  background-color: black;
}
.inner--graphic-design__image {
  width: 100%;
  position: relative;
  bottom: 0px;
  height: 100vh;
  object-fit: cover;
}
.inner--graphic-design__title {
  font-size: 1.7777777778rem;
  color: #25564b;
}
.inner--graphic-design__paragraph {
  font-size: 0.8888888889rem;
  color: #25564b;
  line-height: 1.4444444444rem;
}
.inner--photograpy__image {
  width: 100%;
  height: 100vh;
  position: relative;
  bottom: 0px;
  object-fit: cover;
}
.inner--photograpy__title {
  font-size: 1.7777777778rem;
  color: #19536b;
}
.inner--photograpy__paragraph {
  font-size: 0.8888888889rem;
  color: #19536b;
  line-height: 1.4444444444rem;
}

.content__inner {
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 10%;
  width: 18.0555555556rem;
  height: 8.0555555556rem;
}
.content__inner--plus {
  height: calc(8.0555555556rem + 23px);
}

.container__inner {
  height: inherit;
}

.double {
  text-align: center;
}
.double__image {
  width: 100%;
  height: 100%;
}
.double__image::before {
  color: black;
}
.double__title {
  font-size: 1.7777777778rem;
  color: #23303e;
}
.double__paragraph {
  font-size: 0.8888888889rem;
  color: #818498;
  line-height: 160%;
}
.double__link {
  font-size: 0.8888888889rem;
  color: #23303e;
  letter-spacing: 1px;
}

.container__card {
  width: 20.8333333333rem;
  height: 24.2777777778rem;
}

.content__card {
  width: 17rem;
  height: 16.7777777778rem;
}

.menu {
  z-index: 2;
  background-color: white;
  left: 50%;
  transform: translate(-50%, 0);
  transition: all 0.15s ease-in-out;
}
.menu--visible {
  top: 13%;
  visibility: visible;
  width: 18.2777777778rem;
  height: 16.9444444444rem;
  opacity: 1;
  box-shadow: 0px 2px 12px 6px rgba(0, 0, 0, 0.041);
}
.menu--hidden {
  top: 0%;
  visibility: hidden;
  opacity: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.041);
}
.menu__link {
  font-size: 1rem;
  color: #a7abae;
}
.menu__button {
  width: 140px;
  height: 56px;
  border-radius: 28px;
  border-color: transparent;
  font-size: 1rem;
  background-color: #fad400;
  color: #23303e;
}
.menu__button :hover {
  background-color: green;
}
.menu__button :active {
  opacity: 0;
}

.container__menu {
  top: 13%;
  left: 50%;
  transform: translate(-50%, 0);
}

.content__menu {
  width: 240px;
  height: 220px;
}

.overlay {
  visibility: hidden;
  z-index: 1;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: ease-in-out;
}
.overlay__visible {
  visibility: visible;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  background-color: rgba(255, 255, 255, 0.025);
}
.overlay__hidden {
  visibility: hidden;
  -webkit-backdrop-filter: blur(0px);
  backdrop-filter: blur(0px);
  background-color: rgba(255, 255, 255, 0);
}

header {
  background-color: none;
  position: fixed;
  top: 0px;
  z-index: 3;
}

.container__header {
  width: 100%;
  height: 4.7777777778rem;
}

.navbar__logo {
  font-size: 1.3333333333rem;
  text-decoration: none;
  color: white;
  mix-blend-mode: difference;
}

.content__nav {
  min-width: 18.1666666667rem;
  width: 87.2%;
  height: 1.3333333333rem;
}

main {
  background: inherit;
}

.hero {
  background-color: #3dbeff;
}
.hero__img {
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 0;
}
.hero__svg {
  width: 20%;
  height: 45%;
  object-fit: contain;
  bottom: 100px;
  animation: arrow 3s infinite;
}
.hero__title {
  text-align: center;
  letter-spacing: 2.93vw;
  font-size: 9.06vw;
  color: white;
}
@media (min-width: fun.pxToRem(540px)) {
  .hero__title {
    color: red;
  }
}

.container__hero {
  position: relative;
  overflow-y: hidden;
  height: 100vh;
}

.content__hero {
  left: 50%;
  transform: translate(-50%, 0);
  top: 30%;
  width: 80.8%;
  height: 46.5%;
}

body {
  height: auto;
  min-width: 20.8333333333rem;
  max-width: 80rem;
  overflow-x: hidden;
  overflow-y: scroll;
}

footer {
  background-color: #91d4c6;
  display: fixed;
  height: 348px;
  bottom: 0px;
}

svg.info__title > path {
  fill: #327b6a;
}

.info__socials svg > path {
  fill: #327b6a;
}

.info__socials svg > path:hover {
  fill: white;
}

.shows {
  width: 100%;
}
.shows__pic {
  width: 50%;
}

.info {
  margin: auto auto;
  text-align: center;
}
.info__title {
  font-size: 1.4444444444rem;
  color: #327b6a;
}
.info__links {
  width: 295px;
}
.info__links :hover {
  color: white;
}
.info__links a {
  font-size: 0.8888888889rem;
  color: #327b6a;
}
.info__socials {
  min-width: 164px;
  font-size: 1.4444444444rem;
  color: #327b6a;
}

.container__footer {
  height: 19.4444444444rem;
}
.container__info {
  height: 97px;
}

.content__footer {
  width: 16.3888888889rem;
  height: 11.4444444444rem;
}

.img {
  background-position: center bottom;
  background-size: 30%;
  background-repeat: no-repeat;
  overflow: hidden hidden;
}
.img--hr {
  background-image: url("/images/mobile/image-header.jpg");
  background-size: contain;
  background-color: red;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 60vw auto;
}
.img--gr_ds {
  background-image: url("/images/mobile/image-graphic-design.jpg");
}
.img--ph {
  background-image: url("/images/mobile/image-photography.jpg");
}

@keyframes arrow {
  0% {
    height: 45%;
  }
  50% {
    height: 30%;
  }
  100% {
    height: 45%;
  }
}
