@use '../utilities/functions.scss' as fun;

header {
    background-color: none;
    position: fixed;
    top: 0px;
    z-index: 3;
}

$width-container__header: fun.pxToRem(375px);
$height-container__header: fun.pxToRem(86px);

.container {
    &__header {
        width: 100%;//width: $width-container__header;
        height: $height-container__header;
    }
}